import { useForm } from "react-hook-form";
import { useMutation } from "react-query";
import { FaRegEyeSlash, FaRegEye } from "react-icons/fa6";
import { ThreeDots } from "react-loader-spinner";
// import jwt_decode from "jwt-decode";

// api
import { signInFunction } from "../../api/api";
import { useState } from "react";
import { Button } from "../reusable_components/custom_button";
import { useLocalStorage } from "../../utils/hooks/use_local_storage";
import { LOCAL_STORAGE_KEYS } from "../../utils/local_storage_keys";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { cn } from "../../utils/custom_functions";
import { useDecodeJwt } from "../../utils/hooks/use_decode_jwt";
import { ROUTE_LINKS } from "../../utils/route_links";
import { useTimeout } from "../../utils/hooks/use_time_out";

export default function Signin() {
  const [isPasswordVisible, setIspasswordVisible] = useState(false);
  const { setItem } = useLocalStorage(LOCAL_STORAGE_KEYS.tokens);
  const { decodedJwt, setJwtToken } = useDecodeJwt();
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const { set } = useTimeout(
    () => (window.location.href = ROUTE_LINKS.home),
    5000
  );

  // hooks
  const {
    register,
    handleSubmit,
    formState: { isLoading: isFormLoading, errors, isSubmitting },
  } = useForm();

  const {
    isLoading: isRequestLoading,
    isSuccess,
    mutateAsync: signInAsync,
    isError,
    error,
  } = useMutation("signin", {
    mutationFn: signInFunction,
    onSuccess: (data) => {
      setItem(data);
      setJwtToken(data.access);
      setDisableUserInterface((p) => true);
      set();
    },
    onMutate: (value) => {
      setDisableUserInterface((p) => true);
    },
    onError: (e) => {
      setDisableUserInterface((p) => false);
    },
  });

  // submit hander
  const submitHander = async (data) => {
    try {
      await signInAsync(data);
    } catch (error) {
      // console.log(error);
    }
  };

  return (
    <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
      <div className="rounded-lg p-6 shadow-sm border border-gray-200">
        <h2 className="text-start text-black pb-6 px-2 font-extrabold text-3xl">
          Welcome back
          <p className="text-sm font-normal text-slate-600 pt-2">
            Trust matols logistics services to transport your items safely. Dont
            have an account ?{" "}
            <a href="/user/register" className="text-yellow-400 font-bold">
              Register
            </a>
            .
          </p>
        </h2>
        <form className="px-2" onSubmit={handleSubmit(submitHander)}>
          <div className="rounded-lg shadow-sm">
            <input
              {...register("email", {
                required: "Email address required",
                disabled: disableUserInterface,
              })}
              type="email"
              name="email"
              placeholder="matols@gmail.com"
              className={cn(
                "text-sm text-slate-800  w-full relative shadow-sm block rounded-t-lg border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                {
                  "text-red-500 placeholder:text-red-500": errors.email,
                }
              )}
            />

            <div className="relative">
              <input
                {...register("password", {
                  required: "Password required",
                  disabled: disableUserInterface,
                })}
                type={isPasswordVisible ? "text" : "password"}
                name="password"
                placeholder="Enter password"
                className={cn(
                  "peer text-sm pe-9 text-slate-800 w-full shadow-sm relative block rounded-b-lg border-gray-200 focus:z-10 focus:ring-yellow-400 focus:border-yellow-400 disabled:opacity-50 disabled:pointer-events-none",
                  {
                    " text-red-500 placeholder:text-red-500": errors.password,
                  }
                )}
              />
              <div className="peer-focus:z-10 absolute inset-y-0 right-0 px-3 flex justify-center items-center">
                <div onClick={() => setIspasswordVisible((r) => !r)}>
                  {!isPasswordVisible ? (
                    <FaRegEyeSlash
                      className="text-yellow-400 cursor-pointer"
                      size={20}
                    />
                  ) : (
                    <FaRegEye
                      className="text-yellow-400 cursor-pointer"
                      size={20}
                    />
                  )}
                </div>
              </div>
            </div>
          </div>
          {errors.email && (
            <p className="pt-2 text-red-500 text-sm italic">
              {errors.email.message}
            </p>
          )}
          {errors.password && (
            <p className="pt-2 text-red-500 text-sm italic">
              {errors.password.message}
            </p>
          )}
          <div className="w-full text-end py-4">
            <a
              href={ROUTE_LINKS.requestpasswordreset}
              className="text-yellow-400 text-sm font-bold"
            >
              Forgot password?
            </a>
          </div>
          {/*Network error and success alerts*/}
          {isError && (
            <ErrorAlert
              className="mb-4"
              secondaryText={`${error.response.data.message} ${error.message}`}
            />
          )}
          {isSuccess && (
            <SuccessAlert
              secondaryText={`Welcome back matols Logistics services ${decodedJwt.user_first_name} ${decodedJwt.user_surname}`}
              className="mb-4"
            />
          )}
          {/**/}
          <Button
            className="rounded-lg text-sm shadow-sm "
            type="submit"
            disabled={disableUserInterface}
          >
            {isRequestLoading || isFormLoading ? (
              <div className="w-full h-full flex justify-center items-center">
                <ThreeDots visible width="40" height="20" color="#000" />
              </div>
            ) : (
              "Sign in to your account"
            )}
          </Button>
        </form>
      </div>
    </main>
  );
}
